'use client';

import {useState, useEffect} from 'react';
import {useRouter} from 'next/navigation';
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from '@/components/ui/table';
import {Button} from '@/components/ui/button';
import {Input} from '@/components/ui/input';
import {AlertDialog, AlertDialogTrigger, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogFooter, AlertDialogAction, AlertDialogCancel} from '@/components/ui/alert-dialog';
import {useToast} from '@/hooks/use-toast';
import {Textarea} from '@/components/ui/textarea';
import {Card, CardHeader, CardTitle, CardDescription, CardContent} from '@/components/ui/card';
import {Separator} from '@/components/ui/separator';
import {Icons} from '@/components/icons';
import {auditDataForIndianNames} from '@/ai/flows/audit-data-for-indian-names';
import {provideDataAuditSummary} from '@/ai/flows/provide-data-audit-summary';
import {suggestDataCorrections} from '@/ai/flows/suggest-data-corrections';
import {Toaster} from "@/components/ui/toaster"
import {Avatar, AvatarImage, AvatarFallback} from "@/components/ui/avatar";
import {Label} from "@/components/ui/label";
import {
  getAuth,
  onAuthStateChanged,
  signInAnonymously,
  signOut,
} from 'firebase/auth';
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  doc,
  deleteDoc,
} from 'firebase/firestore';
import {initializeApp} from 'firebase/app';
import { SignIn } from '@/components/sign-in';
import { Loading } from '@/components/loading';

// Define a type for the data, each key is a string, and value can be a string or number
type Data = Record<string, string>;

export default function Home() {
  const [mounted, setMounted] = useState(false);
  const [data, setData] = useState<Data[]>([]);
  const [filename, setFilename] = useState<string>('');
  const [filesize, setFilesize] = useState<number>(0);
  const [modified, setModified] = useState<boolean>(false);
  const [auditResults, setAuditResults] = useState<string>('');
  const [auditSummary, setAuditSummary] = useState<string>('');
  const [dataCorrections, setDataCorrections] = useState<string>('');
  const [loadingAudit, setLoadingAudit] = useState<boolean>(false);
  const [user, setUser] = useState<any>(null);
  const [uploadedFiles, setUploadedFiles] = useState<any[]>([]);
  const [firebaseApp, setFirebaseApp] = useState<any>(null);
  const [firebaseConfig, setFirebaseConfig] = useState<any>(null);
  const [auth, setAuth] = useState<any>(null);
  const [db, setDb] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();
  const [name, setName] = useState('');
  const [result, setResult] = useState('');

  const {toast} = useToast();

  // Set mounted state to true after component mounts
  useEffect(() => {
    setMounted(true);
  }, []);

  // Initialize Firebase only after component mounts
  useEffect(() => {
    if (!mounted) return;

    const config = {
      apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
      authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
      projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
      storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
      messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
      appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
    };
    setFirebaseConfig(config);
      
    if (!firebaseApp && config.apiKey) {
      try {
        const app = initializeApp(config);
        setFirebaseApp(app);
        const authInstance = getAuth(app);
        const dbInstance = getFirestore(app);
        setAuth(authInstance);
        setDb(dbInstance);
        console.log('Firebase initialized');
      } catch (error) {
        console.error('Error initializing Firebase:', error);
      }
    }
  }, [mounted]);

  // Set up auth state listener after Firebase is initialized
  useEffect(() => {
    if (!mounted || !auth) return;

    const unsubscribe = onAuthStateChanged(auth, async (authUser) => {
      if (authUser) {
        setUser(authUser);
        await loadFiles(authUser.uid);
      } else {
        setUser(null);
      }
      setLoading(false);
    });

    return () => unsubscribe();
  }, [mounted, auth]);

  const loadFiles = async (userId: string) => {
    if (!db) {
      console.error('Firestore not initialized');
      return;
    }
    try {
      const filesCollection = collection(db, `users/${userId}/files`);
      const filesSnapshot = await getDocs(filesCollection);
      const filesList = filesSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setUploadedFiles(filesList);
    } catch (error: any) {
      toast({
        title: 'Error loading files',
        description: error.message,
        variant: 'destructive',
      });
      console.error('Error loading files:', error);
    }
  };

  const handleFileUpload = async (file: File) => {
    setFilename(file.name);
    setFilesize(file.size);

    const reader = new FileReader();

    reader.onload = async (e: ProgressEvent<FileReader>) => {
      const result = e.target?.result;
      if (typeof result === 'string') {
        try {
          // Parse CSV data
          const rows = result.split('\n');
          const headers = rows[0].split(',').map(header => header.trim());
          const parsedData: Data[] = rows.slice(1).map(row => {
            const values = row.split(',').map(value => value.trim());
            const rowData: Data = {};
            headers.forEach((header, index) => {
              rowData[header] = values[index] || '';
            });
            return rowData;
          }).filter(row => Object.values(row).some(value => value !== '')); // Filter out empty rows

          // Set the parsed data to state
          setData(parsedData);

          // Upload file details to Firestore
          await uploadFileToFirestore(user.uid, file.name, file.size, result);
          toast({
            title: 'File uploaded successfully',
            description: `${file.name} has been uploaded and parsed.`,
          });
          await loadFiles(user.uid); // Reload files after upload

        } catch (error: any) {
          toast({
            title: 'Error processing file',
            description: error.message,
            variant: 'destructive',
          });
          console.error('Error processing file:', error);
        }
      }
    };
    reader.readAsText(file);
  };

  const uploadFileToFirestore = async (userId: string, fileName: string, fileSize: number, fileData: string) => {
    if (!db) {
      console.error('Firestore not initialized');
      return;
    }
    try {
      const filesCollection = collection(db, `users/${userId}/files`);
      await addDoc(filesCollection, {
        name: fileName,
        size: fileSize,
        data: fileData,
        uploadedAt: new Date(),
      });
    } catch (error: any) {
      console.error('Error uploading file to Firestore:', error);
      throw new Error('Failed to upload file to Firestore');
    }
  };

  const handleSignOut = () => {
    if (!auth) {
      console.error('Auth not initialized');
      return;
    }
    signOut(auth)
      .then(() => {
        console.log('Signed out successfully');
        setUser(null);
        setUploadedFiles([]);
        toast({
          title: 'Signed out',
          description: 'You have been signed out.',
        });
      })
      .catch((error) => {
        console.error('Error signing out:', error);
        toast({
          title: 'Sign out error',
          description: error.message,
          variant: 'destructive',
        });
      });
  };

  const handleCellChange = (index: number, key: string, value: string) => {
    setData(prevData => {
      const newData = [...prevData];
      newData[index][key] = value;
      setModified(true);
      return newData;
    });
  };

  const handleDeleteRow = (index: number) => {
    AlertDialog({children: 'Are you sure you want to delete this row?'});
    setData(prevData => {
      const newData = [...prevData];
      newData.splice(index, 1);
      setModified(true);
      return newData;
    });
  };

  const handleSend = () => {
    console.log('Modified Data:', data);
    setModified(false);
    toast({
      title: 'Data sent!',
      description: 'Modified data logged to console.',
    });
  };

  const handleAuditData = async () => {
    setLoadingAudit(true);
    try {
      // Convert data to the format expected by auditDataForIndianNames
      const auditInput = {data: data};
      const auditOutput = await auditDataForIndianNames(auditInput);
      setAuditResults(JSON.stringify(auditOutput, null, 2));

      // Get audit summary
      const summaryInput = {auditResults: JSON.stringify(auditOutput, null, 2)};
      const summaryOutput = await provideDataAuditSummary(summaryInput);
      setAuditSummary(summaryOutput.summary);

      // Get data corrections
      const dataString = JSON.stringify(data, null, 2);
      const correctionsInput = {data: dataString, auditResults: JSON.stringify(auditOutput, null, 2)};
      const correctionsOutput = await suggestDataCorrections(correctionsInput);
      setDataCorrections(correctionsOutput.suggestions);

    } catch (error: any) {
      toast({
        title: 'Error auditing data',
        description: error.message,
        variant: 'destructive',
      });
      setAuditResults('Error during audit.');
      setAuditSummary('Error during audit.');
      setDataCorrections('Error during audit.');
    } finally {
      setLoadingAudit(false);
    }
  };

  // Add this function to render table headers
  const renderTableHeaders = () => {
    if (data.length === 0) return null;
    const headers = Object.keys(data[0]);
    return (
      <TableHeader>
        <TableRow>
          {headers.map((header) => (
            <TableHead key={header}>{header}</TableHead>
          ))}
          <TableHead>Actions</TableHead>
        </TableRow>
      </TableHeader>
    );
  };

  // Add this function to render table rows
  const renderTableRows = () => {
    return data.map((row, rowIndex) => (
      <TableRow key={rowIndex}>
        {Object.entries(row).map(([key, value]) => (
          <TableCell key={key}>
            <Input
              value={value}
              onChange={(e) => handleCellChange(rowIndex, key, e.target.value)}
            />
          </TableCell>
        ))}
        <TableCell>
          <Button
            variant="destructive"
            size="sm"
            onClick={() => handleDeleteRow(rowIndex)}
          >
            Delete
          </Button>
        </TableCell>
      </TableRow>
    ));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1500));
    setResult(`Corrected name: ${name}`);
    setLoading(false);
  };

  // Show loading state while checking authentication
  if (!mounted || loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  // Show sign-in page if not authenticated
  if (!user) {
    return <SignIn />;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-100 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md mx-auto">
        <div className="relative">
          <div className="absolute -inset-4 bg-white rounded-2xl shadow-neu"></div>
          <div className="relative bg-white rounded-xl p-8 shadow-neu-inner">
            <h1 className="text-3xl font-bold text-center text-gray-800 mb-8">
              Name Corrector
            </h1>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="relative">
                <div className="absolute -inset-1 bg-white rounded-lg shadow-neu"></div>
                <div className="relative">
                  <Input
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    placeholder="Enter name to correct"
                    className="w-full bg-white shadow-neu-inner"
                  />
                </div>
              </div>
              <div className="relative">
                <div className="absolute -inset-1 bg-white rounded-lg shadow-neu"></div>
                <div className="relative">
                  <Button
                    type="submit"
                    className="w-full bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-neu-inner hover:shadow-neu transition-all duration-200"
                  >
                    Correct Name
                  </Button>
                </div>
              </div>
            </form>
            {result && (
              <div className="mt-6 relative">
                <div className="absolute -inset-1 bg-white rounded-lg shadow-neu"></div>
                <div className="relative bg-white rounded-lg p-4 shadow-neu-inner">
                  <p className="text-gray-700">{result}</p>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      <Toaster />
    </div>
  );
}
